body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

.App-Background,
.App-Page {
	position: fixed;
	width: 100%;
	height:100%;
	left:0;
	top:0;
  background: #2F3E4F;
  font-family: 'Sulphur Point', sans-serif;
  font-size: 0.9rem;
  color: #8b8b8b;
}

h2 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 1.4em;
    transform: translate(-50%, -50%);
}

h3 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: left;
    position: absolute;
    width: 40%;
    left: 50%;
    top: 50%;
    font-size: 1.4em;
    transform: translate(-50%, -50%);
}

.Sidebar {
  --color-text: #8b8b8b;
  --color-link-hover: #bdbdbd;
  background: none;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  padding-top: 20px;
  padding-left: 20px;
  font-family: 'Sulphur Point', sans-serif;
}

.Sidebar a {
  padding: 6px 6px 6px 16px;
  text-decoration: none;
  font-size: 20px;
  color: var(--color-text);
  display: block;
  border: none;
  background: none;
  text-align: left;
  cursor: pointer;
  outline: none;
}

.Sidebar a:hover {
  color: var(--color-link-hover);
}

.Info {
	position: absolute;
	bottom: 20px;
  width: 100%;
  left: 20px;
  text-align: left;
  letter-spacing: 0.1em;
  line-height: 1.4em;
  font-family: 'Sulphur Point', sans-serif;
  font-size: 1.1rem;
  color: #d8d7d7;
	z-index: 100;
	display:block;
}

@media screen and (max-width: 600px) {
  h2 {
      letter-spacing: 0em;
      font-size: 1.0em;
  }
  h3 {
      letter-spacing: 0em;
      line-height: 1.5em;
      text-align: left;
      position: absolute;
      width: 50%;
      left: 50%;
      top: 50%;
      font-size: 0.8em;
      transform: translate(-50%, -50%);
  }
  .Sidebar a {
    padding: 6px 6px 6px 10px;
    text-decoration: none;
    font-size: 10px;
    line-height: 0.7em;
    color: var(--color-text);
    display: block;
    border: none;
    background: none;
    text-align: left;
    cursor: pointer;
    outline: none;
  }
  .Info {
      position: absolute; 
      bottom: 10px;
      left: 10px;
      letter-spacing: 0.1em;
      line-height: 1.0em;
      font-size: 0.7rem;
  }
}


