body {
    background: #e6e4e4;
}

.Project {
    --color-text: #8b8b8b;
    --color-bg:  #e6e4e4;
    --color-link-hover: #f1f1f1;
    background: var(--color-bg);
    margin: 0;
    padding: 0;
    overflow: hidden;
    font-family: 'Sulphur Point', sans-serif;
    font-size: 0.9rem;
    color: var(--color-text);
    height: 100%;
}

.Project-Grid {
    max-width: 1215px;
    margin:3% auto;
    display: flex;
    align-items: center;
    justify-content: center;
    /* align-content: flex-start; */
    flex-wrap: wrap;
}
    
.Project-Item {
    width:512px;
    height:512px;
    box-sizing: border-box;
    margin:10px;
    transition: transform .25s ease-out, -webkit-filter .25s ease-out;
    transition: transform .25s ease-out, filter .25s ease-out;
    transition: transform .25s ease-out, filter .25s ease-out, -webkit-filter .25s ease-out;
    -webkit-filter: grayscale(60%);
            filter: grayscale(60%);
} 
    
.Project-Item:hover {
    transform:scale(.95);
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}
    
.Project-Content {
    /* overflow: scroll; */
    max-width: 60%;
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    padding-top:5%;
    padding-left:5%;
    font-size: 1.5rem;
    letter-spacing: 0.09em;
    line-height: 1.3em;
} 

.Project-Content-TwoColumn {
    /* overflow: scroll; */
    max-width: 60%;
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    padding-top:20px;
    padding-left:20px;
    padding-right:20px;
}

#blocker {
    /* align-items: center;
    justify-content: center; */
    position: absolute;
    /* display: flex; */
    /* height: 100vh; */
    width: 100%;
    height: 100%;
}
    
#instructions {
    width: 100%;
    height: 100%;
    display: flex;

    -webkit-box-orient: horizontal;
    box-orient: horizontal;
    box-pack: center;
    box-align: center;
    
    color: #ffffff;
    text-align: center;
    font-size: 14px;
    line-height: 24px;
    
    cursor: pointer;
}

.pointer_wrapper { 
    position: fixed; 
    top: 50%; 
    left: 50%; 
    transform: translate(-50%, -50%); 
}

@media (max-width: 1215px) {
    .Project-Grid {
        max-width: 900px;
    }
} 
    
@media (max-width: 915px) {
    .Project-Grid {
        max-width: 600px;
    }
}
    
@media screen and (max-width: 600px) {
    .Project-Grid {
        max-width: 300px;
    }
    .Project-Item {
        max-width: 280px;
        max-height: 280px;
    }
    .Project-Content {
        font-size: 0.7rem;
        letter-spacing: 0.09em;
        line-height: 1.3em;
    }
}
