.cssCanvas {
  position: absolute;
  top: 0;
  overflow: visible;
  z-index: 0;
}

.customSprite {
  position: absolute;
  /* z-index: 2; */
  transform: translate(-50%, -50%);
  font-size: 15px;
  color: beige;
}

.description {
  color: #ffffff;
  font-family: monospace;
  /* pointer-events: none; */
  font-size: 10px;
  position: absolute;
  left: 25px;
  padding: 1em;
  width: 100px;
  background: rgba(0, 0, 0, 0.66);
  border-radius: .5em;
  transition: opacity .5s;
  display: block;
  /* display: none; */
}

.description:hover {
  box-shadow: 0px 0px 12px rgba(0,255,255,0.75);
  border: 1px solid rgba(210, 250, 250, 0.75);
  cursor: pointer;
}