@import url(https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap);
  html {
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }

body {
    background: #e6e4e4;
  }

.Sketch {
    --color-text: #8b8b8b;
    --color-bg:  #e6e4e4;
    --color-link-hover: #f1f1f1;
    background: var(--color-bg);
    margin: 0;
    padding: 0;
    overflow: hidden;
    font-family: 'Sulphur Point', sans-serif;
    font-size: 0.9rem;
    color: var(--color-text);
    height: 100%;
  }

.Sketch-Grid {
    max-width: 1215px;
    margin:0 auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    padding-top:50px;
}
    
@media (max-width: 1215px) {
    .Sketch-Grid {
        max-width: 900px;
    }
}
    
@media (max-width: 915px) {
    .Sketch-Grid {
        max-width: 600px;
    }
}
    
@media (max-width: 615px) {
    .Sketch-Grid {
        max-width: 300px;
    }
}
    
.Sketch-Item {
    width:280px;
    height:280px;
    box-sizing: border-box;
    margin:10px;
    transition: transform .25s ease-out, filter .25s ease-out;
    filter: grayscale(60%);
}    
    
.Sketch-Item:hover {
    transform:scale(.95);
    filter: grayscale(0%);
}
    
.Sketch-Content {
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100%;
    border:0;
    top:0;
    left:0;
}  

    
svg {
    width: 20px;
    height: 20px;
    fill: #AAA; 
}